
'use client';
import { useAuth } from '@/contexts/AuthContext';
import DashboardCard from '@/app/components/DashboardCard';

const AdminPage = () => {
  const { userRole } = useAuth();

  if (userRole !== 'admin') {
    return (
      <div className="flex justify-center items-center h-full">
        <p className="text-red-500">No tienes permiso para ver esta página.</p>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Panel de Administración</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        <DashboardCard
          title="Clientes y Mascotas"
          description="Ver y gestionar la información de los clientes y sus mascotas."
          link="/admin/clientes"
        />
        <DashboardCard
          title="Productos"
          description="Añadir, editar y eliminar productos de la tienda."
          link="/admin/productos"
        />
        <DashboardCard
          title="Turnos"
          description="Administrar los turnos futuros, pasados y del día."
          link="/admin/turnos"
        />
      </div>
    </div>
  );
};

export default AdminPage;
